import React from "react"
import { Section } from "../common/section.component"
import { RichText } from "../common/rich-text.component"

/**
 * Text Slice Object
 * @typedef {
 * {
 *  label: string,
 *  type: string,
 *  primary: {
 *    bgColor: string,
 *    bgImage: *,
 *    title: string,
 *    text: string
 *  }
 *  }
 * } TextSlice
 */

/**
 *
 * @param {TextSlice} slice
 * @return {*}
 * @constructor
 */

export const TextSlice = ({ slice }) => {
  const { bgImage, bgColor, text, title, anchor } = slice.primary
  return (
    <Section className="mb-5 p-4" backgroundImage={bgImage} backgroundColor={bgColor}>
      <div id={anchor} />
      <div>
        <div className="mb-4">
          <RichText render={title} />
        </div>
        <div className="mt-1">
          <RichText render={text} />
        </div>
      </div>
    </Section>
  )
}
